import { useRef, useState, useEffect } from "react";

const ImgData1: string[] = [];
for (let i = 1; i <= 16; i++) {
  ImgData1.push(`web${i}.png`);
}

const ImgData2: string[] = [];
for (let i = 1; i <= 16; i++) {
  ImgData2.push(`web${i}.png`);
}

function MarqueeText({
  direction,
  imgData,
  isRight,
}: {
  direction: number;
  imgData: string[];
  isRight: boolean;
}) {
  const elementRef = useRef<HTMLParagraphElement | null>(null);
  const [count, setCount] = useState<number>(0);

  useEffect(() => {
    const element = elementRef.current;
    if (!element) return;

    const marqueeText = () => {
      element.style.transform = `translate3d(${direction * count}px, 0, 0)`;
    };

    const animationFrame = requestAnimationFrame(marqueeText);

    return () => cancelAnimationFrame(animationFrame);
  }, [count, direction]);

  const [scrollDir, setScrollDir] = useState("scrolling down");

  useEffect(() => {
    const threshold = 0;
    let lastScrollY = window.pageYOffset;
    let ticking = false;

    const updateScrollDir = () => {
      const scrollY = window.pageYOffset;

      if (Math.abs(scrollY - lastScrollY) < threshold) {
        ticking = false;
        return;
      }
      setScrollDir(scrollY > lastScrollY ? "scrolling down" : "scrolling up");
      scrollDir === "scrolling down"
        ? setCount((prev) => prev + 20)
        : setCount((prev) => prev - 20);
      lastScrollY = scrollY > 0 ? scrollY : 0;
      ticking = false;
    };

    const onScroll = () => {
      if (!ticking) {
        window.requestAnimationFrame(updateScrollDir);
        ticking = true;
      }
    };

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [scrollDir]);

  return (
    <div ref={elementRef}>
      <div css={srollcoverStyle(isRight)}>
        {imgData.map((item, idx) => {
          return <img src={item} css={imgStyle} />;
        })}
      </div>
    </div>
  );
}

